@import './bs-import';
.title {
	font-family: 'Nunito Sans', 'Helvetica Neue', sans-serif;
	font-weight: 900;
	font-size: 2rem;
	margin-block: 10px;
	text-align: center;
}
.paymentMessage {
	color: #404f5e;
	font-family: 'Nunito Sans', 'Helvetica Neue', sans-serif;
	font-size: 0.8rem;
	line-height: 1rem;
	margin-inline: 0.5rem;
	text-align: center;
	@include media-breakpoint-up('md') {
		font-size: 1rem;
		line-height: 1.5rem;
	}
}
.checkmark-success {
	font-size: 50px;
	line-height: 100px;
	@include media-breakpoint-up('md') {
		font-size: 150px;
		line-height: 185px;
	}
}
.checkmark-error {
	font-size: 50px;
	line-height: 100px;
	@include media-breakpoint-up('md') {
		font-size: 150px;
		line-height: 195px;
	}
}
.customcard {
	background: white;
	padding: 60px;
	border-radius: 10px;
	box-shadow: 0 2px 3px #c8d0d8;
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin: 0 auto;
}
.wrapper-circle {
	border-radius: 200px;
	height: 100px;
	width: 100px;
	background: hsla(0, 0%, 70%, 0.744);
	margin: 0 auto;
	display: flex;
	justify-content: center;
	@include media-breakpoint-up('md') {
		height: 200px;
		width: 200px;
	}
}
.success-color{
  color: #9abc66;
}
.error-color{
  color:#d5412a;
}
