#hero {
	width: 100%;
	height: 80vh;
	background: #37517e;
}
#hero .container {
	padding-top: 72px;
}
#hero h1 {
	margin: 0 0 10px 0;
	font-size: 48px;
	font-weight: 700;
	line-height: 56px;
	color: #fff;
}
#hero h2 {
	color: rgba(255, 255, 255, 0.6);
	margin-bottom: 50px;
	font-size: 24px;
}
#hero .btn-get-started {
	font-family: 'Jost', sans-serif;
	font-weight: 500;
	font-size: 16px;
	letter-spacing: 1px;
	display: inline-block;
	padding: 10px 28px 11px 28px;
	border-radius: 50px;
	transition: 0.5s;
	margin: 10px 0 0 0;
	color: #fff;
	background: #47b2e4;
}
#hero .btn-get-started:hover {
	background: #209dd8;
}

#hero .btn-watch-video:hover i {
	color: #47b2e4;
}
#hero .animated {
	animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@media (max-width: 991px) {
	#hero {
		height: 100vh;
		text-align: center;
	}
	#hero .animated {
		-webkit-animation: none;
		animation: none;
	}
	#hero .hero-img {
		text-align: center;
	}
	#hero .hero-img img {
		width: 50%;
	}
}
@media (max-width: 768px) {
	#hero h1 {
		font-size: 28px;
		line-height: 36px;
	}
	#hero h2 {
		font-size: 18px;
		line-height: 24px;
		margin-bottom: 30px;
	}
	#hero .hero-img img {
		width: 70%;
	}
}
@media (max-width: 575px) {
	#hero .hero-img img {
		width: 80%;
	}
	#hero .btn-get-started {
		font-size: 16px;
		padding: 10px 24px 11px 24px;
	}
}

@-webkit-keyframes up-down {
	0% {
		transform: translateY(10px);
	}
	100% {
		transform: translateY(-10px);
	}
}

@keyframes up-down {
	0% {
		transform: translateY(10px);
	}
	100% {
		transform: translateY(-10px);
	}
}
/*--------------------------------------------------------------
# Sections General
--------------------------------------------------------------*/
section {
	padding: 60px 0;
	overflow: hidden;
}

.section-bg {
	background-color: #f3f5fa;
}

.section-title {
	text-align: center;
	padding-bottom: 30px;
}
.section-title h2 {
	font-size: 32px;
	font-weight: bold;
	text-transform: uppercase;
	margin-bottom: 20px;
	padding-bottom: 20px;
	position: relative;
	color: #37517e;
}
.section-title h2::before {
	content: '';
	position: absolute;
	display: block;
	width: 120px;
	height: 1px;
	background: #ddd;
	bottom: 1px;
	left: calc(50% - 60px);
}
.section-title h2::after {
	content: '';
	position: absolute;
	display: block;
	width: 40px;
	height: 3px;
	background: #47b2e4;
	bottom: 0;
	left: calc(50% - 20px);
}
.section-title p {
	margin-bottom: 0;
}

/*--------------------------------------------------------------
# Clients
--------------------------------------------------------------*/
.clients {
	padding: 12px 0;
	text-align: center;
}
.clients img {
	max-width: 45%;
	transition: all 0.4s ease-in-out;
	display: inline-block;
	padding: 15px 0;
	-webkit-filter: grayscale(100);
	filter: grayscale(100);
}
.clients img:hover {
	-webkit-filter: none;
	filter: none;
	transform: scale(1.1);
}
@media (max-width: 768px) {
	.clients img {
		max-width: 40%;
	}
}

/*--------------------------------------------------------------
# About Us
--------------------------------------------------------------*/
.about .content h3 {
	font-weight: 600;
	font-size: 26px;
}
.about .content ul {
	list-style: none;
	padding: 0;
}
.about .content ul li {
	padding-left: 28px;
	position: relative;
}
.about .content ul li + li {
	margin-top: 10px;
}
.about .content ul i {
	position: absolute;
	left: 0;
	top: 2px;
	font-size: 20px;
	color: #47b2e4;
	line-height: 1;
}
.about .content p:last-child {
	margin-bottom: 0;
	line-height: 1.8rem;
}
.about .content .btn-learn-more {
	font-family: 'Poppins', sans-serif;
	font-weight: 500;
	font-size: 14px;
	letter-spacing: 1px;
	display: inline-block;
	padding: 12px 32px;
	border-radius: 4px;
	transition: 0.3s;
	line-height: 1;
	color: #47b2e4;
	-webkit-animation-delay: 0.8s;
	animation-delay: 0.8s;
	margin-top: 6px;
	border: 2px solid #47b2e4;
}
.about .content .btn-learn-more:hover {
	background: #47b2e4;
	color: #fff;
	text-decoration: none;
}

/*--------------------------------------------------------------
# Why Us
--------------------------------------------------------------*/
.why-us .content {
	padding: 60px 100px 0 100px;
}
.why-us .content h3 {
	font-weight: 400;
	font-size: 34px;
	color: #37517e;
}
.why-us .content h4 {
	font-size: 20px;
	font-weight: 700;
	margin-top: 5px;
}
.why-us .content p {
	font-size: 15px;
	color: #848484;
}
.why-us .img {
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center center;
}
.why-us .accordion-list {
	padding: 0 100px 60px 100px;
}
.why-us .accordion-list ul {
	padding: 0;
	list-style: none;
}
.why-us .accordion-list li + li {
	margin-top: 15px;
}
.why-us .accordion-list li {
	padding: 20px;
	background: #fff;
	border-radius: 4px;
}
.why-us .accordion-list a {
	display: block;
	position: relative;
	font-family: 'Poppins', sans-serif;
	font-size: 16px;
	line-height: 24px;
	font-weight: 500;
	padding-right: 30px;
	outline: none;
	cursor: pointer;
}
.why-us .accordion-list span {
	color: #47b2e4;
	font-weight: 600;
	font-size: 18px;
	padding-right: 10px;
}
.why-us .accordion-list i {
	font-size: 24px;
	position: absolute;
	right: 0;
	top: 0;
}
.why-us .accordion-list p {
	margin-bottom: 0;
	padding: 10px 0 0 0;
}
.why-us .accordion-list .icon-show {
	display: none;
}
.why-us .accordion-list a.collapsed {
	color: #343a40;
}
.why-us .accordion-list a.collapsed:hover {
	color: #47b2e4;
}
.why-us .accordion-list a.collapsed .icon-show {
	display: inline-block;
}
.why-us .accordion-list a.collapsed .icon-close {
	display: none;
}
@media (max-width: 1024px) {
	.why-us .content,
	.why-us .accordion-list {
		padding-left: 0;
		padding-right: 0;
	}
}
@media (max-width: 992px) {
	.why-us .img {
		min-height: 400px;
	}
	.why-us .content {
		padding-top: 30px;
	}
	.why-us .accordion-list {
		padding-bottom: 30px;
	}
}
@media (max-width: 575px) {
	.why-us .img {
		min-height: 200px;
	}
}

/*--------------------------------------------------------------
# Skills
--------------------------------------------------------------*/
.skills .content h3 {
	font-weight: 700;
	font-size: 32px;
	color: #37517e;
	font-family: 'Poppins', sans-serif;
}
.skills .content ul {
	list-style: none;
	padding: 0;
}
.skills .content ul li {
	padding-bottom: 10px;
}
.skills .content ul i {
	font-size: 20px;
	padding-right: 4px;
	color: #47b2e4;
}
.skills .content p:last-child {
	margin-bottom: 0;
}
.skills .progress {
	height: 60px;
	display: block;
	background: none;
	border-radius: 0;
}
.skills .progress .skill {
	padding: 0;
	margin: 0 0 6px 0;
	text-transform: uppercase;
	display: block;
	font-weight: 600;
	font-family: 'Poppins', sans-serif;
	color: #37517e;
}
.skills .progress .skill .val {
	float: right;
	font-style: normal;
}
.skills .progress-bar-wrap {
	background: #e8edf5;
	height: 10px;
}
.skills .progress-bar {
	width: 1px;
	height: 10px;
	transition: 0.9s;
	background-color: #4668a2;
}

/*--------------------------------------------------------------
# Services
--------------------------------------------------------------*/
.services .icon-box {
	box-shadow: 0px 0 25px 0 rgba(0, 0, 0, 0.1);
	padding: 50px 30px;
	transition: all ease-in-out 0.4s;
	background: #fff;
}
.services .icon-box .icon {
	margin-bottom: 10px;
}
.services .icon-box .icon i {
	color: #47b2e4;
	font-size: 36px;
	transition: 0.3s;
}
.services .icon-box h4 {
	font-weight: 500;
	margin-bottom: 15px;
	font-size: 24px;
}
.services .icon-box h4 a {
	color: #37517e;
	transition: ease-in-out 0.3s;
}
.services .icon-box p {
	line-height: 24px;
	font-size: 14px;
	margin-bottom: 0;
}
.services .icon-box:hover {
	transform: translateY(-10px);
}
.services .icon-box:hover h4 a {
	color: #47b2e4;
}

/*--------------------------------------------------------------
# Cta
--------------------------------------------------------------*/
.cta {
	background: linear-gradient(rgba(40, 58, 90, 0.9), rgba(40, 58, 90, 0.9)),
		url('../../img/cta-bg.jpg') fixed center center;
	background-size: cover;
	padding: 120px 0;
}
.cta h3 {
	color: #fff;
	font-size: 28px;
	font-weight: 700;
}
.cta p {
	color: #fff;
}
.cta .cta-btn {
	font-family: 'Jost', sans-serif;
	font-weight: 500;
	font-size: 16px;
	letter-spacing: 1px;
	display: inline-block;
	padding: 12px 40px;
	border-radius: 50px;
	transition: 0.5s;
	margin: 10px;
	border: 2px solid #fff;
	color: #fff;
}
.cta .cta-btn:hover {
	background: #47b2e4;
	border: 2px solid #47b2e4;
}
@media (max-width: 1024px) {
	.cta {
		background-attachment: scroll;
	}
}
@media (min-width: 769px) {
	.cta .cta-btn-container {
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}
}

/*--------------------------------------------------------------
# Portfolio
--------------------------------------------------------------*/
.portfolio #portfolio-flters {
	list-style: none;
	margin-bottom: 20px;
}
.portfolio #portfolio-flters li {
	cursor: pointer;
	display: inline-block;
	margin: 10px 5px;
	font-size: 15px;
	font-weight: 500;
	line-height: 1;
	color: #444444;
	transition: all 0.3s;
	padding: 8px 20px;
	border-radius: 50px;
	font-family: 'Poppins', sans-serif;
}
.portfolio #portfolio-flters li:hover,
.portfolio #portfolio-flters li.filter-active {
	background: #47b2e4;
	color: #fff;
}
.portfolio .portfolio-item {
	margin-bottom: 30px;
}
.portfolio .portfolio-item .portfolio-img {
	overflow: hidden;
}
.portfolio .portfolio-item .portfolio-img img {
	transition: all 0.6s;
}
.portfolio .portfolio-item .portfolio-info {
	opacity: 0;
	position: absolute;
	left: 15px;
	bottom: 0;
	z-index: 3;
	right: 15px;
	transition: all 0.3s;
	background: rgba(55, 81, 126, 0.8);
	padding: 10px 15px;
}
.portfolio .portfolio-item .portfolio-info h4 {
	font-size: 18px;
	color: #fff;
	font-weight: 600;
	color: #fff;
	margin-bottom: 0px;
}
.portfolio .portfolio-item .portfolio-info p {
	color: #f9fcfe;
	font-size: 14px;
	margin-bottom: 0;
}
.portfolio .portfolio-item .portfolio-info .preview-link,
.portfolio .portfolio-item .portfolio-info .details-link {
	position: absolute;
	right: 40px;
	font-size: 24px;
	top: calc(50% - 18px);
	color: #fff;
	transition: 0.3s;
}
.portfolio .portfolio-item .portfolio-info .preview-link:hover,
.portfolio .portfolio-item .portfolio-info .details-link:hover {
	color: #47b2e4;
}
.portfolio .portfolio-item .portfolio-info .details-link {
	right: 10px;
}
.portfolio .portfolio-item:hover .portfolio-img img {
	transform: scale(1.15);
}
.portfolio .portfolio-item:hover .portfolio-info {
	opacity: 1;
}

/*--------------------------------------------------------------
# Portfolio Details
--------------------------------------------------------------*/
.portfolio-details {
	padding-top: 40px;
}
.portfolio-details .portfolio-details-slider img {
	width: 100%;
}
.portfolio-details .portfolio-details-slider .swiper-pagination {
	margin-top: 20px;
	position: relative;
}
.portfolio-details
	.portfolio-details-slider
	.swiper-pagination
	.swiper-pagination-bullet {
	width: 12px;
	height: 12px;
	background-color: #fff;
	opacity: 1;
	border: 1px solid #47b2e4;
}
.portfolio-details
	.portfolio-details-slider
	.swiper-pagination
	.swiper-pagination-bullet-active {
	background-color: #47b2e4;
}
.portfolio-details .portfolio-info {
	padding: 30px;
	box-shadow: 0px 0 30px rgba(55, 81, 126, 0.08);
}
.portfolio-details .portfolio-info h3 {
	font-size: 22px;
	font-weight: 700;
	margin-bottom: 20px;
	padding-bottom: 20px;
	border-bottom: 1px solid #eee;
}
.portfolio-details .portfolio-info ul {
	list-style: none;
	padding: 0;
	font-size: 15px;
}
.portfolio-details .portfolio-info ul li + li {
	margin-top: 10px;
}
.portfolio-details .portfolio-description {
	padding-top: 30px;
}
.portfolio-details .portfolio-description h2 {
	font-size: 26px;
	font-weight: 700;
	margin-bottom: 20px;
}
.portfolio-details .portfolio-description p {
	padding: 0;
}

/*--------------------------------------------------------------
# Team
--------------------------------------------------------------*/
.team .member {
	position: relative;
	box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
	padding: 30px;
	border-radius: 5px;
	background: #fff;
	transition: 0.5s;
}
.team .member .pic {
	overflow: hidden;
	width: 180px;
	border-radius: 50%;
}
.team .member .pic img {
	transition: ease-in-out 0.3s;
}
.team .member:hover {
	transform: translateY(-10px);
}
.team .member .member-info {
	padding-left: 30px;
}
.team .member h4 {
	font-weight: 700;
	margin-bottom: 5px;
	font-size: 20px;
	color: #37517e;
}
.team .member span {
	display: block;
	font-size: 15px;
	padding-bottom: 10px;
	position: relative;
	font-weight: 500;
}
.team .member span::after {
	content: '';
	position: absolute;
	display: block;
	width: 50px;
	height: 1px;
	background: #cbd6e9;
	bottom: 0;
	left: 0;
}
.team .member p {
	margin: 10px 0 0 0;
	font-size: 14px;
}
.team .member .social {
	margin-top: 12px;
	display: flex;
	align-items: center;
	justify-content: flex-start;
}
.team .member .social a {
	transition: ease-in-out 0.3s;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50px;
	width: 32px;
	height: 32px;
	background: #eff2f8;
}
.team .member .social a i {
	color: #37517e;
	font-size: 16px;
	margin: 0 2px;
}
.team .member .social a:hover {
	background: #47b2e4;
}
.team .member .social a:hover i {
	color: #fff;
}
.team .member .social a + a {
	margin-left: 8px;
}

.faq .faq-list {
	padding: 0 100px;
}
.faq .faq-list ul {
	padding: 0;
	list-style: none;
}
.faq .faq-list li + li {
	margin-top: 15px;
}
.faq .faq-list li {
	padding: 20px;
	background: #fff;
	border-radius: 4px;
	position: relative;
}
.faq .faq-list a {
	display: block;
	position: relative;
	font-family: 'Poppins', sans-serif;
	font-size: 16px;
	line-height: 24px;
	font-weight: 500;
	padding: 0 30px;
	outline: none;
	cursor: pointer;
}
.faq .faq-list .icon-help {
	font-size: 24px;
	position: absolute;
	right: 0;
	left: 20px;
	color: #47b2e4;
}
.faq .faq-list .icon-show,
.faq .faq-list .icon-close {
	font-size: 24px;
	position: absolute;
	right: 0;
	top: 0;
}
.faq .faq-list p {
	margin-bottom: 0;
	padding: 10px 0 0 0;
}
.faq .faq-list .icon-show {
	display: none;
}
.faq .faq-list a.collapsed {
	color: #37517e;
	transition: 0.3s;
}
.faq .faq-list a.collapsed:hover {
	color: #47b2e4;
}
.faq .faq-list a.collapsed .icon-show {
	display: inline-block;
}
.faq .faq-list a.collapsed .icon-close {
	display: none;
}
@media (max-width: 1200px) {
	.faq .faq-list {
		padding: 0;
	}
}

@-webkit-keyframes animate-loading {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
@keyframes animate-loading {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
